.head{
    height: 20%;
    width: 100%;
    background-color: hsl(10, 79%, 65%);
    border-radius: 8px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 4%;
    color: hsl(33, 100%, 98%);
    align-items: center;
}

h1{
    margin: 0;
    font-size: 2rem;
}


@media only screen and (max-width: 770px){
    h1{
        font-size: 1.8rem;
    }
}

@media only screen and (max-width: 480px){
    h1{
        font-size: 1.7rem;
    }
}