.column{
    height: fit-content;
    border-radius: 30px;
    width: 90%;
}
.sec{
    height: 22px;
    color: transparent;
    border: none;
    outline: none;
}
.bar{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 12%;
}
.bar:hover{
    cursor: pointer;
}
.amount{
    width: 100%;
    height: 30px;
    padding: 4%;
    background-color: hsl(25, 47%, 15%);
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    margin-bottom: 4px;
    font-size: .8rem;
    font-weight: bold;
}
@media only screen and (max-width: 770px){
    .amount{
        font-size: .7rem;
    }
}
@media only screen and (max-width: 480px){
    .amount{
        font-size: .6rem;
    }
}

