
.label{
    width: 100%;
    display: flex;
    justify-content: center;
    font-size: .8em;
    color: hsl(28, 10%, 53%);
}

@media only screen and (max-width: 770px){
    .label{
        font-size: .7rem;
    }
}

@media only screen and (max-width: 480px){
    .label{
        font-size: .6rem;
    }
}