.body{
    background-color: hsl(33, 100%, 98%);
    border-radius: 8px;
    padding: 4% 6%;
    width: 100%;
    height: 80%;
}

hr{
    height: 1.5px;
    border: none;
    background-color: hsl(27, 66%, 92%);
}

.foot{
    height: 25%;
    display: flex;
    flex-direction: column;
}

.subfoot{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

div{
    height: fit-content;
}
.total{
    font-size: 2rem;
    margin: 0;
}
.ttxt, .ptxt, .pcnt{
    font-size: .9rem;
}
.ttxt, .ptxt{
    color: hsl(28, 10%, 53%);

}
.pcnt{
    color: black;
    font-weight: bold;
}

.htxt{
    color: hsl(25, 47%, 15%);
    margin: 0;
    height: 15%;
}
.sub2{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

@media only screen and (max-width: 770px){
    .ttxt, .ptxt, .pcnt{
        font-size: .8rem;
    }
    .total{
        font-size: 1.8rem;
    }
    .htxt{
        font-size: 1.3rem;
    }
    .body{
        height: 70%;
        gap: 2%;
    }
}

@media only screen and (max-width: 480px){
    .ttxt, .ptxt, .pcnt{
        font-size: .7rem;
    }
    .total{
        font-size: 1.7rem;
    }
    .htxt{
        font-size: 1.2rem;
    }
}